import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

class App extends Component {
  constructor(props){
    super(props)

    this.state={
      counter:0,
      showError : false
    }
  }
  render() {
    const styles = {
      display:this.state.showError
    }
    return (
      <div data-test="component-App">
      <h1 data-test="counter-display">The counter is {this.state.counter}</h1>
      <h3 data-test="error-component" style={styles}>The counter can not go  below 0.</h3>
        <button data-test="increment" onClick={()=>this.setState({counter:this.state.counter+1})}>Increment</button>
      </div>
    );
  }
}

export default App;
